.header {
  display: flex;
  font-size: 20px;
  color: black;
  padding: 1rem 2rem;
  margin-bottom: 0.5rem;
  justify-content: space-between;
  align-items: center;
  background: white;
}

.title {
  display: flex;
  font-size: 12px;
}

.search {
  display: flex;
  flex: 0.6;
  align-items: center;
  justify-content: center;
}

.search svg {
  margin-right: 0.5rem;
}

.search input {
  width: 100%;
  border: none;
  outline: none;
}

.user {
  display: flex;
  font-size: 14px;
  align-items: center;

}

.profile {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
}

.profile img {
  object-fit: cover;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}